import React from 'react';
import MysteryIntroLayout from '../../components/MysteryIntroLayout';
import { useFragmentsTranslation } from '../../hooks/useFragmentsTranslation';

const ChronosParadoxIntro: React.FC = () => {
  const { t, originalLang } = useFragmentsTranslation();
  
  const previewCards = [
    {
      icon: '',
      title: originalLang === 'kr' ? '  ' : 'Time Travel Device',
      description: originalLang === 'kr' ? '   ' : 'Time travel prototype developed by the doctor',
      gradientColors: ['#667eea', '#764ba2']
    },
    {
      icon: '',
      title: originalLang === 'kr' ? ' ' : 'Quantum Fragments',
      description: originalLang === 'kr' ? '    ' : 'Mysterious substance created by spacetime collapse',
      gradientColors: ['#f093fb', '#f5576c']
    },
    {
      icon: '',
      title: originalLang === 'kr' ? '  ' : 'Time Log Data',
      description: originalLang === 'kr' ? '   ' : 'Secret experimental records of the doctor',
      gradientColors: ['#4facfe', '#00f2fe']
    }
  ];

  const featureCards = [
    {
      icon: '',
      title: originalLang === 'kr' ? 'SF  ' : 'Sci-Fi Time Travel Mystery',
      description: originalLang === 'kr' ? '      ' : 'Experience a futuristic detective game where science meets philosophy'
    },
    {
      icon: '',
      title: originalLang === 'kr' ? '  ' : 'Innovative Deduction System',
      description: originalLang === 'kr' ? '       ' : 'A new form of mystery that overturns conventional detective game wisdom'
    },
    {
      icon: '',
      title: originalLang === 'kr' ? '  ' : 'Spacetime Manipulation',
      description: originalLang === 'kr' ? '     ' : 'Approach the truth by unraveling the secrets of time and space'
    }
  ];

  return (
    <MysteryIntroLayout
      seoTitle={originalLang === 'kr'
        ? "  -   2 SF  "
        : "Chronos Paradox - Fragment of Mystery Case2 Sci-Fi Time Travel Mystery"
      }
      seoDescription={originalLang === 'kr'
        ? "     ! AI, ,     ?      .   2 SF !"
        : " Dr. Chronos disappeared from the time research institute! Who is the culprit among AI, time travelers, and rival scientists? Solve complex mysteries involving quantum physics and time travel. Fragment of Mystery Case2 Sci-Fi detective game!"
      }
      seoKeywords={originalLang === 'kr'
        ? " ,   2, SF, , , , , , SF, , "
        : "chronos paradox, fragment of mystery case2, sci-fi detective game, time travel game, quantum physics game, future mystery, science detective, spacetime puzzle, sci-fi mystery game, time paradox, time travel detective"
      }
      canonicalUrl="/fragments/case2"
      title={t.case2.title}
      subtitle={t.case2.subtitle}
      previewCards={previewCards}
      featureCards={featureCards}
      storyTitle={t.case2.storyTitle}
      storyContent={t.case2.storyContent}
      gameUrl="game"
      backUrl="/fragments"
      caseId="case2"
      uiCustomization={{
        backgroundGradient: 'linear-gradient(135deg, #0c0c0c 0%, #1a1a2e 25%, #16213e 50%, #0f3460 75%, #1e3c72 100%)',
        titleGradient: 'linear-gradient(45deg, #667eea, #764ba2, #f093fb)',
        accentColor: '#667eea',
        buttonGradient: 'linear-gradient(45deg, #667eea, #764ba2)',
        fontFamily: "'Orbitron', 'Noto Sans KR', sans-serif",
        titleIcon: '',
        gameButtonIcon: '',
        gameButtonText: originalLang === 'kr' ? '  ' : 'Start Spacetime Investigation'
      }}
    />
  );
};

export default ChronosParadoxIntro;