import React, { useState, useEffect } from 'react';
import MysteryGameLayout from '../../components/MysteryGameLayout';
import { useLanguage } from '../../hooks/useLanguage';
import type { GameScenario } from './scenario_kr';
import type { CaseFeedbackData } from './feedbackData_kr';

const ChronosParadoxGame: React.FC = () => {
  const { language, t } = useLanguage();
  const [scenario, setScenario] = useState<GameScenario | null>(null);
  const [feedbackData, setFeedbackData] = useState<CaseFeedbackData | null>(null);
  const [loading, setLoading] = useState(true);

  //    
  useEffect(() => {
    const loadLanguageFiles = async () => {
      setLoading(true);
      try {
        const scenarioModule = await import(`./scenario_${language}`);
        const feedbackModule = await import(`./feedbackData_${language}`);
        
        setScenario(scenarioModule.chronosParadoxScenario);
        setFeedbackData(feedbackModule.default);
      } catch (error) {
        console.error('Failed to load language files:', error);
        // :   
        try {
          const scenarioModule = await import('./scenario_kr');
          const feedbackModule = await import('./feedbackData_kr');
          setScenario(scenarioModule.chronosParadoxScenario);
          setFeedbackData(feedbackModule.default);
        } catch (fallbackError) {
          console.error('Failed to load fallback files:', fallbackError);
        }
      } finally {
        setLoading(false);
      }
    };

    loadLanguageFiles();
  }, [language]);

  if (loading || !scenario || !feedbackData) {
    return (
      <div style={{
        minHeight: '100vh',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        background: 'linear-gradient(135deg, #0c0c0c 0%, #1a1a2e 50%, #667eea 100%)',
        color: 'white',
        fontSize: '1.2rem'
      }}>
        {t('loading', 'Loading...')}
      </div>
    );
  }

  // SEO   
  const seoContent = language === 'en' ? {
    title: "Chronos Paradox Play - Fragments of Mystery Case2 SF Game",
    description: " Play now! Uncover the secrets of the Time Research Institute. SF mystery game analyzing quantum fragments and time logs to solve Dr. Chronos's disappearance. Play Fragments of Mystery Case2 free!",
    keywords: "chronos paradox play, fragments of mystery case2 game, SF mystery play, time travel game online, quantum physics mystery, time paradox solve, SF mystery play"
  } : {
    title: "   -   2 SF",
    description: "   !    .           SF .   2  !",
    keywords: "  ,   2 , SF  ,  ,  ,  , SF  "
  };

  return (
    <MysteryGameLayout
      scenario={scenario}
      caseId="case2"
      maxHints={5}
      seoTitle={seoContent.title}
      seoDescription={seoContent.description}
      seoKeywords={seoContent.keywords}
      canonicalUrl="/fragments/case2"
      backUrl="/fragments/case2"
      uiCustomization={{
        cardStyles: {
          suspectColor: '#8b5cf6',    //  - SF 
          evidenceColor: '#06b6d4',   //  -  
          locationColor: '#10b981',   //  - 
          discoveredBorderColor: '#667eea', // -
          selectedBorderColor: '#f093fb',   // -
          cardBackground: 'rgba(102, 126, 234, 0.1)', //  
          cardHoverEffect: 'rgba(102, 126, 234, 0.2)'
        },
        backgroundPattern: {
          type: 'grid',  //  
          opacity: 0.05,
          color: '#667eea'
        },
        typography: {
          headingFont: "'Orbitron', sans-serif",     // SF  
          bodyFont: "'Noto Sans KR', sans-serif"
        },
        animations: {
          cardHoverScale: 1.08,        //    
          connectionButtonPulse: true,
          resultScreenEffect: 'sparkles'  // SF 
        },
        layout: {
          cardBorderRadius: '16px',    //   
          panelBorderRadius: '20px'
        }
      }}
      themeColors={{
        primary: '#0c0c0c',      //  
        secondary: '#1a1a2e',    //    
        accent: '#667eea'        // - 
      }}
      cardIcon=""
      feedbackData={feedbackData}
    />
  );
};

export default ChronosParadoxGame;