import React from 'react';
import { Link, Routes, Route, useLocation } from 'react-router-dom';
import Case1Routes from './games/case1/Routes';
import ChronosParadoxRoutes from './games/case2/Routes';
import SEOHead from '../../components/SEOHead';
import { LanguageContext, useLanguageState } from './hooks/useLanguage';
import LanguageSelector from './components/LanguageSelector';
import { useFragmentsTranslation } from './hooks/useFragmentsTranslation';

//  : LanguageProvider  useFragmentsTranslation 
const FragmentsContent: React.FC = () => {
  const location = useLocation();
  const isInCasePage = location.pathname.match(/\/case[12]/); //  1, 2 
  const { t, originalLang } = useFragmentsTranslation(); // Fragments 

  return (
    <>
      <SEOHead
        title={originalLang === 'kr' 
          ? "  (Fragment of Mystery) - 2   "
          : "Fragment of Mystery - 2 Cases Free Online Detective Game"
        }
        description={originalLang === 'kr'
          ? "        !   SF   2   .        ."
          : " Advanced detective game where you combine clue cards to solve mysterious cases! Play 2 free cases: mansion murder mystery and sci-fi time travel mystery. Online browser game that develops logical thinking and reasoning skills."
        }
        keywords={originalLang === 'kr'
          ? " , Fragment of Mystery, , , , , , , , , , , , SF, , , , , "
          : "Fragment of Mystery, detective game, mystery game, clue combination, card game, logic puzzle, free game, online game, browser game, find culprit, solve case, mansion murder, sci-fi mystery, time travel game, reasoning game, detective game, brain game, puzzle game"
        }
        canonical="/fragments/"
      >
        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "Game",
            "name": "  (Fragment of Mystery)",
            "description": "        ",
            "url": "https://norri.netlify.app/fragments/",
            "genre": [" ", " ", " "],
            "gamePlatform": "Web Browser",
            "operatingSystem": "Any",
            "applicationCategory": "Game",
            "isAccessibleForFree": true,
            "inLanguage": "ko-KR",
            "offers": {
              "@type": "Offer",
              "price": "0",
              "priceCurrency": "KRW",
              "availability": "https://schema.org/InStock"
            },
            "creator": {
              "@type": "Organization",
              "name": "Norri Puzzle Games",
              "url": "https://norri.netlify.app/"
            },
            "publisher": {
              "@type": "Organization", 
              "name": "Norri Puzzle Games",
              "url": "https://norri.netlify.app/"
            },
            "gameItem": [
              {
                "@type": "Game",
                "name": " 1:   ", 
                "description": "       ",
                "url": "https://norri.netlify.app/fragments/case1/",
                "genre": " ",
                "gamePlatform": "Web Browser",
                "isAccessibleForFree": true
              },
              {
                "@type": "Game", 
                "name": " 2:  ",
                "description": "      SF  ",
                "url": "https://norri.netlify.app/fragments/case2/",
                "genre": "SF  ", 
                "gamePlatform": "Web Browser",
                "isAccessibleForFree": true
              }
            ],
            "aggregateRating": {
              "@type": "AggregateRating",
              "ratingValue": "4.8",
              "ratingCount": "127",
              "bestRating": "5",
              "worstRating": "1"
            },
            "review": [
              {
                "@type": "Review",
                "reviewRating": {
                  "@type": "Rating", 
                  "ratingValue": "5",
                  "bestRating": "5"
                },
                "author": {
                  "@type": "Person",
                  "name": ""
                },
                "reviewBody": "   .       !"
              }
            ]
          })}
        </script>
        
        <meta property="og:title" content="  (Fragment of Mystery) -   " />
        <meta property="og:description" content="        !   SF   2   ." />
        <meta property="og:type" content="website" />
        <meta property="og:url" content="https://norri.netlify.app/fragments/" />
        <meta property="og:image" content="https://norri.netlify.app/images/fragments-preview.jpg" />
        <meta property="og:site_name" content="Norri  " />
        <meta property="og:locale" content="ko_KR" />
        
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:title" content="  (Fragment of Mystery) -   " />
        <meta name="twitter:description" content="        !   SF   2   ." />
        <meta name="twitter:image" content="https://norri.netlify.app/images/fragments-preview.jpg" />
        
        <meta name="robots" content="index, follow, max-image-preview:large, max-snippet:-1, max-video-preview:-1" />
        <meta name="googlebot" content="index, follow" />
        <link rel="alternate" hrefLang="ko" href="https://norri.netlify.app/fragments/" />
      </SEOHead>

      {isInCasePage ? (
        <Routes>
          <Route path="case1/*" element={<Case1Routes />} />
          <Route path="case2/*" element={<ChronosParadoxRoutes />} />
        </Routes>
      ) : (
        <div style={{
          minHeight: '100vh',
          background: 'linear-gradient(135deg, #1a1a2e 0%, #16213e 100%)',
          padding: 'clamp(1rem, 4vw, 2rem) clamp(0.5rem, 2vw, 1rem)',
          color: 'white'
        }}>
          {/*   - /PC   */}
          <div style={{
            position: 'absolute',
            top: 'clamp(1rem, 3vw, 1.5rem)',
            right: 'clamp(0.5rem, calc(50vw - 600px + 1rem), 1rem)', // : , PC: 1200px 
            zIndex: 10
          }}>
            <LanguageSelector />
          </div>

          <div style={{
            maxWidth: '1200px',
            margin: '0 auto'
          }}>

            {/*  */}
            <div style={{
              textAlign: 'center',
              marginBottom: '3rem',
              marginTop: 'clamp(4rem, 8vw, 2rem)' //    
            }}>
              <h1 style={{
                fontSize: 'clamp(2rem, 6vw, 4rem)',
                margin: '0 0 1rem 0',
                fontWeight: 'bold',
                textShadow: '2px 2px 4px rgba(0,0,0,0.3)'
              }}>
                 {t.mainTitle}
              </h1>
              <div style={{
                fontSize: 'clamp(1rem, 2.5vw, 1.5rem)',
                margin: '0 0 1rem 0',
                opacity: 0.8,
                fontWeight: 400,
                textShadow: '1px 1px 2px rgba(0,0,0,0.3)'
              }}>
                {originalLang === 'kr' ? '[ ]' : '[Fragment of Mystery]'}
              </div>
              
              <p style={{
                fontSize: 'clamp(1rem, 3vw, 1.3rem)',
                margin: '0 auto',
                opacity: '0.9',
                maxWidth: '600px'
              }}>
                {t.mainSubtitle}
              </p>
            </div>

            {/*   */}
            <div style={{
              display: 'grid',
              gridTemplateColumns: 'repeat(auto-fit, minmax(min(350px, 100%), 1fr))',
              gap: 'clamp(1rem, 4vw, 2rem)',
              maxWidth: '900px',
              margin: '0 auto',
              padding: '0 clamp(0.5rem, 2vw, 1rem)' //   
            }}>
              
              {/*  1:   */}
              <Link to="/fragments/case1" style={{
                textDecoration: 'none',
                color: 'inherit'
              }}>
                <div style={{
                  background: 'rgba(255, 255, 255, 0.1)',
                  borderRadius: '20px',
                  padding: 'clamp(1.5rem, 4vw, 2rem)',
                  backdropFilter: 'blur(10px)',
                  border: '1px solid rgba(255, 255, 255, 0.2)',
                  transition: 'all 0.3s ease',
                  cursor: 'pointer',
                  position: 'relative',
                  overflow: 'hidden'
                }}
                onMouseEnter={(e) => {
                  e.currentTarget.style.transform = 'translateY(-5px)';
                  e.currentTarget.style.boxShadow = '0 20px 40px rgba(0,0,0,0.2)';
                }}
                onMouseLeave={(e) => {
                  e.currentTarget.style.transform = 'translateY(0)';
                  e.currentTarget.style.boxShadow = 'none';
                }}>
                  
                  {/*   */}
                  <div style={{
                    position: 'absolute',
                    top: '-50%',
                    right: '-50%',
                    width: '200%',
                    height: '200%',
                    background: 'linear-gradient(45deg, transparent 40%, rgba(139, 69, 19, 0.1) 50%, transparent 60%)',
                    transform: 'rotate(-15deg)',
                    pointerEvents: 'none'
                  }} />
                  
                  <div style={{ position: 'relative', zIndex: 1 }}>
                    <div style={{
                      fontSize: '3rem',
                      marginBottom: '1rem',
                      textAlign: 'center'
                    }}>
                      
                    </div>
                    
                    <h3 style={{
                      fontSize: '1.5rem',
                      margin: '0 0 1rem 0',
                      fontWeight: 'bold',
                      textAlign: 'center'
                    }}>
                      {t.caseTitle} 1: {t.case1.title}
                    </h3>
                    
                    <p style={{
                      fontSize: '1rem',
                      margin: '0 0 1.5rem 0',
                      opacity: '0.9',
                      lineHeight: '1.6',
                      textAlign: 'center'
                    }}>
                      {t.case1.description}
                    </p>
                    
                    <div style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      fontSize: '0.9rem',
                      opacity: '0.8'
                    }}>
                      <span> {t.difficulty}: </span>
                      <span> {t.estimatedTime}: 10-15{t.minutes}</span>
                    </div>
                  </div>
                </div>
              </Link>

              {/*  2:   */}
              <Link to="/fragments/case2" style={{
                textDecoration: 'none',
                color: 'inherit'
              }}>
                <div style={{
                  background: 'rgba(255, 255, 255, 0.1)',
                  borderRadius: '20px',
                  padding: 'clamp(1.5rem, 4vw, 2rem)',
                  backdropFilter: 'blur(10px)',
                  border: '1px solid rgba(255, 255, 255, 0.2)',
                  transition: 'all 0.3s ease',
                  cursor: 'pointer',
                  position: 'relative',
                  overflow: 'hidden'
                }}
                onMouseEnter={(e) => {
                  e.currentTarget.style.transform = 'translateY(-5px)';
                  e.currentTarget.style.boxShadow = '0 20px 40px rgba(0,0,0,0.2)';
                }}
                onMouseLeave={(e) => {
                  e.currentTarget.style.transform = 'translateY(0)';
                  e.currentTarget.style.boxShadow = 'none';
                }}>
                  
                  {/* SF   */}
                  <div style={{
                    position: 'absolute',
                    top: '-50%',
                    right: '-50%',
                    width: '200%',
                    height: '200%',
                    background: 'linear-gradient(45deg, transparent 40%, rgba(102, 126, 234, 0.2) 50%, transparent 60%)',
                    transform: 'rotate(15deg)',
                    pointerEvents: 'none'
                  }} />
                  
                  <div style={{ position: 'relative', zIndex: 1 }}>
                    <div style={{
                      fontSize: '3rem',
                      marginBottom: '1rem',
                      textAlign: 'center'
                    }}>
                      
                    </div>
                    
                    <h3 style={{
                      fontSize: '1.5rem',
                      margin: '0 0 1rem 0',
                      fontWeight: 'bold',
                      textAlign: 'center'
                    }}>
                      {t.caseTitle} 2: {t.case2.title}
                    </h3>
                    
                    <p style={{
                      fontSize: '1rem',
                      margin: '0 0 1.5rem 0',
                      opacity: '0.9',
                      lineHeight: '1.6',
                      textAlign: 'center'
                    }}>
                      {t.case2.description}
                    </p>
                    
                    <div style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      fontSize: '0.9rem',
                      opacity: '0.8'
                    }}>
                      <span> {t.difficulty}: </span>
                      <span> {t.estimatedTime}: 20-30{t.minutes}</span>
                    </div>
                  </div>
                </div>
              </Link>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

//  : LanguageProvider 
export const FragmentsIndex: React.FC = () => {
  const languageState = useLanguageState(); //   

  return (
    <LanguageContext.Provider value={languageState}>
      <FragmentsContent />
    </LanguageContext.Provider>
  );
};

export default FragmentsIndex;